header {
  position: relative;
  background-color: var(--dark-clr);
}
@media (min-width: 768px) {
  header {
    position: absolute;
    width: 100%;
    z-index: 2;
    background-color: transparent;
  }
}
header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 15px;
}
header .brand {
  font-size: 1.25rem;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--white-clr);
}
header .nav-toggle {
  width: 30px;
  height: 25px;
  cursor: pointer;
  transition: var(--transition);
}
@media (min-width: 768px) {
  header .nav-toggle {
    display: none;
  }
}
header .nav-toggle span {
  display: block;
  height: 2px;
  width: 100%;
  transition: var(--transition);
  background-color: var(--white-clr);
}
header .nav-toggle.active span {
  transform: translateY(11px) rotate(45deg);
  background-color: var(--danger-clr);
}
header .nav-toggle span::before, 
header .nav-toggle span::after {
  content: "";
  display: block;
  height: 2px;
  width: 100%;
  transition: var(--transition);
  background-color: var(--white-clr);
}
header .nav-toggle span::before {
  transform: translateY(10px);
}
header .nav-toggle.active span::before {
  opacity: 0;
  transform: translate(-30px, 11px);
  background-color: var(--danger-clr);
}
header .nav-toggle span::after {
  transform: translateY(18px);
}
header .nav-toggle.active span::after {
  transform: translateY(-2px) rotate(-90deg);
  background-color: var(--danger-clr);
}
header nav {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-inline-start: 15px;
  transition: var(--transition);
  background-color: var(--dark-clr);
}
header .nav-toggle.active + nav {
  height: 285px;
  z-index: 2;
}
@media (min-width: 768px) {
  header nav {
    position: static;
    height: unset;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
  }
  header nav ul {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    column-gap: 20px;
  }
}
header nav ul li a {
  display: block;
  width: 100%;
  font-weight: 600;
  height: 35px;
  line-height: 2;
  text-transform: capitalize;
  color: var(--light-clr);
  transition: var(--transition);
}
header nav ul li a:is(:hover, :active, :focus, .active) {
  color: var(--white-clr);
}
@media (min-width: 768px) {
  header nav ul li a {
    display: inline-block;
  }
}
header nav button {
  width: 100px;
  height: 30px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  border-radius: 8px;
  color: var(--white-clr);
  transition: var(--transition);
  background-color: var(--primary-clr);
}
header nav button:is(:hover, :active, :focus) {
  background-color: var(--accent-alt-clr);
}
@media (min-width: 768px) {
  header nav button:is(:hover, :active, :focus) {
  transform: translateY(-3px);
}
}
