#blog .articles {
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(250px, 350px) );
  place-content: center;
  gap: 3.125rem;
  margin-block-start: 3.5rem;
}
#blog article {
  transition: var(--transition);
}
#blog article:hover {
  transform: translateY(-1rem);
}
#blog article span,
#blog article p {
  display: block;
  margin-top: 10px;
  font-size: 14px;
  color: var(--text-clr);
}
#blog article h3 {
  margin-block: 10px;
  font-weight: 500;
}
#blog article a {
  display: block;
  margin-top: 20px;
  text-transform: capitalize;
  color: var(--primary-clr);
}
#blog article a:hover {
  color: var(--black-clr);
}
#blog article a svg {
  font-size: 14px;
  margin-inline-start: 5px;
}
