#services {
  background-color: var(--white-clr);
}
#services .content {
  display: grid;
  place-items: center;
  gap: 30px;
  padding-block-start: 50px;
}
@media (min-width:768px) {
  #services .content {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width:992px) {
  #services .content {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
#services .box {
  text-align: center;
  transition: var(--transition);
} 
#services .box:hover {
  transform: translateY(-10px);
}
#services .box svg {
  padding: 20px;
  font-size: 25px;
  border-radius: 50%;
  transition: var(--transition);
  color: var(--primary-clr);
  box-shadow: var(--box-shadow)
}
#services .box:hover svg {
  color: var(--white-clr);
  background-color: var(--primary-clr);
}
#services .box h3 {
  font-weight: 500;
  margin-block: 15px;
}
#services .box p {
  font-size: 14px;
  text-align: center;
  margin: 0 auto;
  color: var(--text-clr);
}
@media (min-width:992px) {
  #services .box p {
    max-width: 300px;
  }
}