.testimonials {
  background-color: var(--white-clr);
}
.testimonials .testimonials__list {
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(250px, 300px) );
  place-content: center;
  gap: 3.125rem;
  text-align: center;
  margin-block-start: 3.5rem;
}
.testimonials .testimonials__list .testimonial {
  transition: var(--transition)
}
.testimonials .testimonials__list .testimonial:hover {
  transform: translateY(-1rem);
}
.testimonials .testimonials__list .testimonial img {
  position: relative;
  bottom: -30px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  outline-offset: 3px;
  outline: 0.2px solid var(--text-clr);
}
.testimonials .testimonials__list .testimonial q {
  display: block;
  font-style: italic;
  font-size: 14px;
  color: var(--text-clr);
  padding: 50px 20px 20px;
  min-height: 180px;
  box-shadow: var(--box-shadow);
}
.testimonials .testimonials__list .testimonial span {
  display: inline-block;
  margin-top: 15px;
}
.testimonials .testimonials__list .testimonial span:last-child {
  font-size: 14px;
  color: var(--text-clr);
}