#pricing .pricing__lists {
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(250px, 300px) );
  place-content: center;
  gap: 3.125rem;
  margin-block-start: 3.125rem;
}
#pricing .pricing__list {
  padding-block: 40px;
  text-align: center;
  border-radius: 5px;
  transition: var(--transition);
  box-shadow: var(--box-shadow)
}
#pricing .pricing__list:hover {
  transform: translateY(-1rem);
}
#pricing .pricing__list .header span {
  display: block;
}
#pricing .pricing__list .header span:first-child {
  font-size: 15px;
  text-transform: uppercase;
}
#pricing .pricing__list .header span:nth-child(2) {
  font-size: 35px;
  font-weight: 600;
}
#pricing .pricing__list .header span:last-child {
  font-size: 14px;
  text-transform: uppercase;
  color: var(--text-clr);
}
#pricing .pricing__list ul {
  padding-top: 20px;
}
#pricing .pricing__list ul li:not(:last-child) {
  margin-block-end: 20px;
}
#pricing .pricing__list ul li span {
  color: var(--primary-clr);
}
#pricing .pricing__list button {
  width: 120px;
  height: 40px;
  border: none;
  margin-top: 20px;
  cursor: pointer;
  font-weight: 600;
  border-radius: 5px;
  color: var(--white-clr);
  text-transform: capitalize;
  transition: var(--transition);
  background-color: var(--primary-clr);
}
#pricing .pricing__list button:is(:hover, :active, :focus) {
  background-color: var(--accent-alt-clr);
}
