.get__started {
  padding-block: 125px;
  position: relative;
  background: lightgreen url('../../../assets/images/get-started-bg.jpg') no-repeat center;
  background-size: cover;
}
.get__started::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  opacity: 0.9;
  background: linear-gradient(to right, #512da8, #711e72)
}
.get__started .container {
  position: relative;
  text-align: center;
  z-index: 2;
}
.get__started h2, .get__started p {
  color: var(--white-clr);
}
.get__started button {
  width: 140px;
  height: 40px;
  border: none;
  cursor: pointer;
  margin-top: 30px;
  font-weight: 600;
  border-radius: 5px;
  text-transform: capitalize;
  transition: var(--transition);
}
@media (min-width: 768px) {
  .get__started button:is(:hover, :active, :focus) {
    transform: translateY(-3px);
  }
}
.get__started button svg {
  margin-inline-start: 10px;
}
