.work__process {
  background-color: var(--background-clr);
  text-align: center;
}
.work__process .process__steps {
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(200px, 250px) );
  place-content: center;
  gap: 3.125rem;
  margin-block-start: 3.5rem;
}
.work__process .process__steps .step {
  position: relative;
}
@media (min-width: 992px) {
  .work__process .process__steps .step:first-child::before,
  .work__process .process__steps .step:last-child::before  {
    content: '';
    position: absolute;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    top: 0;
    z-index: 2;
    color: var(--white-clr);
    background-color: var(--primary-clr);
  }
  .work__process .process__steps .step:first-child::before {
    right:-50px;
  }
  .work__process .process__steps .step:last-child::before {
    left:-50px;
  }
  .work__process .process__steps .step:nth-child(2)::before,
  .work__process .process__steps .step:nth-child(2)::after {
    content: '';
    position: absolute;
    height: 0;
    width: 200px;
    top: 20px;
    border-bottom: 1px dotted var(--black-clr);
  }
  .work__process .process__steps .step:nth-child(2)::before {
    left: -125px;
  }
  .work__process .process__steps .step:nth-child(2)::after {
    right: -125px;
  }
}
.work__process .process__steps .step svg {
  font-size: 30px;
  color: var(--primary-clr);
}
.work__process .process__steps .step h3 {
  margin-block: 15px;
  font-weight: 500;
}
.work__process .process__steps .step p {
  font-size: 14px;
  color: var(--text-clr);
}
.work__process button {
  width: 140px;
  height: 40px;
  border: none;
  cursor: pointer;
  margin-top: 30px;
  font-weight: 600;
  border-radius: 5px;
  color: var(--white-clr);
  text-transform: capitalize;
  transition: var(--transition);
  background-color: var(--primary-clr);
}
.work__process button:is(:hover, :active, :focus) {
  background-color: var(--accent-alt-clr);
}
@media (min-width: 768px) {
  .work__process button:is(:hover, :active, :focus) {
    transform: translateY(-3px);
  }
}
.work__process button svg {
  margin-inline-start: 10px;
}
