footer {
  padding-block-start: 50px;
  background-color: var(--dark-clr);
}
footer > .container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 50px;
  padding-block-end: 50px;
}
@media (min-width: 992px) {
  footer > .container {
    flex-direction: row;
  }
}
footer .container .col {
  flex-basis: 25%;
}
footer .container .col:not(:last-of-type) {
  flex-basis: 15%;
}
footer .container .col:last-of-type {
  flex-basis: 55%;
}
footer .container .col h3 {
  font-weight: 500;
  text-transform: capitalize;
  color: var(--white-clr);
}
footer .container .col ul {
  margin-block-start: 10px;
}
footer .container .col ul li a {
  font-size: 14px;
  text-transform: capitalize;
  color: var(--text-clr);
}
footer .container .col p {
  max-width: 600px;
  margin-block-start: 10px;
  color: var(--text-clr);
}
footer .container .col form {
  max-width: 550px;
  margin-block: 20px;
  background-color: #2e313a;
}
footer .container .col form input {
  width: 90%;
  padding: 10px;
  outline: none;
  border: none;
  caret-color: var(--white-clr);
  background-color: transparent;
}
footer .container .col form input::placeholder {
  color: var(--white-clr);
}
footer .container .col form button {
  width: 10%;
  outline: none;
  border: none;
  cursor: pointer;
  text-align: start;
  background-color: transparent;
}
footer .container .col form button svg {
  font-size: 20px;
  color: var(--white-clr);
}
footer .copyright {
  padding-block: 15px;
  background-color: #2e313a;
}
@media (min-width: 768px) {
  footer .copyright .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
footer .copyright p {
  padding-bottom: 15px;
  color: var(--text-clr)
}
footer .copyright img {
  width: 200px;
}