/* Here You can customize the generale styles: */

/* <> How to organize the CSS properties: */
/* 1 - >> display stuff: */
/* 2 - >> positioning stuff: */
/* 3 - >> box-model stuff: */
/* 4 - >> typography: */
/* 5 - >> manipulation: */
/* 6 - >> misc: */

/* ========= Start Global Rules =========*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

body::-webkit-scrollbar-track {
  background-color: lightblue;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: lightblue;
}

body::-webkit-scrollbar-corner {
  border-radius: 10px;
  background-color: lightblue;
}

video {
  height: auto;
  max-width: 100%;
}

img {
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

ul {
  list-style: none;
}

::selection {
  color: #fff;
  background: var(--primary-clr);
}

/* ========= End Global Rules ===========*/

/* ====== Start Typography Rules ========*/

body {
  line-height: 1.5;
  overflow-x: hidden;
  font-family: var(--body-font);
  background-color: var(--background-clr);
}
.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  /* === tablet screens ===  */
  @media (min-width: 768px) {
    width: 750px;
  }

  /* === laptop screens ===  */
  @media (min-width: 992px) {
    width: 970px;
  }

  /* === desktop screens === */
  @media (min-width: 1200px) {
    width: 1170px;
  }

  /* === tv screens === */
  @media (min-width: 1400px) {
    width: 1370px;
  }
}

section {
  padding-block: 3.125rem;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--title-font);
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;

  @media (min-width: 992px) {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

/* h3 {
  font-size: rem(24);
  margin-bottom: rem(24);
} */

a,
a:visited,
a:active {
  text-decoration: none;
}

a:visited {
  color: lightblue;
}

/* ======= End Typography Rules =========*/
/* ========== Start Variables ===========*/

:root {
  /* === colors === */

  --white-clr: #fff;
  --black-clr: #000;
  /* #eeefef */
  --light-clr: #bababa;
  --dark-clr: #272a33;

  --foreground-clr: linear-gradient(
    to bottom,
    #dbd8d6 0%,
    #ededec 13%,
    #ededec 86%,
    #dbd8d6 99%,
    #dbd8d6 100%
  );
  --background-clr: #f8fbff;

  --title-clr: #535362;
  --text-clr: #95a0ab;

  --primary-clr: #fb3e3e;
  --primary-alt-clr: #21688e7f;

  --danger-clr: #d20000;

  --secondary-clr: #9f2f0a;
  --secondary-alt-clr: #7a2103;

  --accent-clr: #e97900;
  --accent-alt-clr: #eb4c03;

  /* === Family Fonts === */
  --body-font: 'Poppins', sans-serif;
  --title-font: "open Sans", sans-serif;

  /* === Box Shadows === */
  --box-shadow: 0 0 1.25rem rgba(108, 118, 134, 0.1);

  /* === Line Height === */
  --line-height-lg: 2;

  /* === Transition === */
  --transition: 0.5s;

  /* === Border Radius === */
  --border-radius: 5px;
  --border-radius-lg: 10px;

  /* === Spaces === */
  --space-sm: 0.625rem;
  --space-md: 1.25rem;
  --space-lg: 3.125rem;
  --space-xlg: 6.25rem;
}

/* =========== End Variables ============*/

/* ======= Start common Styles ========= */

.section__title {
  position: relative;
  text-align: center;
  text-transform: uppercase;
  width: fit-content;
  margin: 0 auto;
  letter-spacing: 2px;
  font-size: 25px;
  font-weight: 500;
  padding-bottom: 50px;
}
.section__title::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 50px;
  left: 50%;
  bottom: 25px;
  transform: translateX(-50%);
  background-color: var(--primary-clr);
}
.section__paragraph {
  text-align: center;
  max-width: 700px;
  margin: 0 auto;
  color: var(--text-clr);
}
/* ========= End common Styles ========= */

