.banner {
  padding-block: 125px;
  position: relative;
  background: lightgreen url('../../../assets/images/banner-bg.jpg') no-repeat center;
  background-size: cover;
}
.banner::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  opacity: 0.9;
  background: linear-gradient(to right, #512da8, #711e72)
}
.banner .container {
  position: relative;
  text-align: center;
  z-index: 2;
}
.banner h2 {
  font-size: 30px;
  letter-spacing: 2px;
  font-weight: 500;
  color: var(--white-clr);
}
.banner p {
  max-width: 500px;
  margin: 0 auto;
  color: var(--text-clr);
}
.banner button {
  margin-top: 25px;
  padding: 10px 20px;
  cursor: pointer;
  text-transform: capitalize;
  color: var(--black-clr);
}
