#landing {
  position: relative;
  height: calc(100vh - 50px);
  background: lightgreen url('../../../assets/images/landing.jpg') no-repeat center center;
  background-size: cover;
}
@media (min-width: 768px) {
  #landing {
    height: 100vh;
  }
}
#landing::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  opacity: 0.9;
  background: linear-gradient(to right, #512da8, #711e72)
}
#landing .container {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  z-index: 1;
  text-align: center;
}
#landing .container h1 {
  font-size: 35px;
  line-height: 2;
  max-width: 500px;
  color: var(--white-clr);
}
#landing .container p {
  line-height: 1.5;
  margin-block-end: 20px;
  color: var(--light-clr);
}
#landing .container button {
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid var(--primary-clr);
}
#landing .container button svg {
  font-size: 18px;
  color: var(--primary-clr);
}
