#contact {
  padding-block-end: 0;
  background-color: var(--white-clr);
}
#contact .contact__info {
  margin-block: 50px;
}
@media (min-width: 768px) {
  #contact .contact__info {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    column-gap: 50px;
  }
}
#contact .contact__info .adresses {
  margin-block: 50px;
  flex-basis: 25%;
}
@media (min-width: 768px) {
  #contact .contact__info .adresses {
    margin-block: 10px;
  }
}
#contact .contact__info .adresses h4 {
  font-weight: 500;
  margin-top: 10px;
  text-transform: capitalize;
}
#contact .contact__info .adresses address {
  color: var(--text-clr);
}
#contact .contact__info .adresses address:not(:last-of-type) {
  margin-top: 10px;
}
#contact .contact__info .adresses time {
  color: var(--text-clr);
}
#contact .contact__info form {
  flex-basis: 50%;
  order: -1;
}
#contact .contact__info form input {
  display: block;
  height: 40px;
  width: 100%;
  padding: 10px;
  outline: none;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
}
#contact .contact__info form label {
  display: block;
  margin-block: 20px;
  width: 100%;
}
#contact .contact__info form div {
  margin-block: 20px;
}
@media (min-width: 768px) {
  #contact .contact__info form div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 10px;
  }
  #contact .contact__info form div label {
    margin-block: 0;
  }
}
#contact .contact__info form textarea {
  width: 100%;
  height: 120px;
  padding: 10px;
  margin-bottom: 15px;
  resize: none;
  outline: none;
  overflow-y: auto;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
}
#contact .contact__info form button {
  width: 140px;
  height: 40px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  border-radius: 5px;
  color: var(--white-clr);
  text-transform: capitalize;
  transition: var(--transition);
  background-color: var(--primary-clr);
}
#contact .contact__info form button:is(:hover, :active, :focus) {
  background-color: var(--accent-alt-clr);
}
@media (min-width: 768px) {
  #contact .contact__info form button:is(:hover, :active, :focus) {
    transform: translateY(-3px);
  }
}
#contact .social__media {
  padding-block: 50px;
  background-color: var(--background-clr);
}
@media (min-width: 992px) {
  #contact .social__media .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
#contact .social__media ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  column-gap: 20px;
  margin-bottom: 30px;
}
@media (min-width: 992px) {
  #contact .social__media ul {
    margin-bottom: 0;
  }
}
#contact .social__media ul li a {
  display: grid;
  place-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #afb5bd;
}
#contact .social__media ul li a:hover {
  border-color: var(--primary-clr);;
}
#contact .social__media ul li a svg {
  height: 20px;
  transition: var(--transition);
  color: #afb5bd;
}
#contact .social__media ul li:hover svg {
  color: var(--primary-clr);
}
#contact .social__media .container .fast__contact a {
  display: block;
  margin-top: 10px;
  font-size: 20px;
  color: var(--dark-clr);
}
@media (min-width: 992px) {
  #contact .social__media .container .fast__contact a {
    display: inline-block;
    margin-inline-start: 20px;
  }
}
#contact .social__media .container .fast__contact a svg {
  margin-inline-end: 10px;
}
