section#features {
  background-color: var(--background-clr);
}
#features .container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 50px;
}
@media (min-width:768px) {
  #features .container {
    flex-direction: row;
    gap: 50px;
  }
  #features .column:last-of-type {
    order: -1;
    flex-basis: 50%;
  }
}
@media (min-width:470px) {
  #features img {
    display: block;
    margin: 0 auto;
    max-width: 300px;
  }
}
@media (min-width:992px) {
  #features img {
    max-width: 500px;
  }
}
#features h2 {
  font-weight: 500;
}
#features p {
  font-size: .9375rem;
  color: var(--text-clr);
}
#features ul {
  font-size: .9375rem;
  list-style: disc;
  margin-block: 20px;
  list-style-position: inside;
  color: var(--text-clr);
}
#features ul ::marker {
  font-size: 20px;
  color: var(--primary-clr);
}
#features button {
  width: 120px;
  height: 40px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  border-radius: 5px;
  color: var(--white-clr);
  text-transform: capitalize;
  transition: var(--transition);
  background-color: var(--primary-clr);
}
#features button:is(:hover, :active, :focus) {
  background-color: var(--accent-alt-clr);
}
@media (min-width: 768px) {
  #features button:is(:hover, :active, :focus) {
    transform: translateY(-3px);
  }
}
#features button svg {
  margin-inline-start: 10px;
}